.setDetailsTabs {
    display: flex;
    flex-wrap: nowrap;
}

.setDetailsTab {
    background-color: transparent;
    border-top: 2px solid var(--border-clr);
    border-left: 2px solid var(--border-clr);
    border-right: 2px solid var(--border-clr);
    border-top-left-radius: 7px;
    border-top-right-radius: 7px;
    color: var(--clr-txt);
    cursor: pointer;
    font-size: 1rem;
    opacity: 0.9;
    margin: 0 0.1rem 0.5rem;
    min-height: 35px;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
}

.setDetailsTab.active {
    background-image: var(--tab-gradient);
    opacity: 1;
}

.setDetailsTab:hover {
    background-image: var(--tab-gradient);
    font-size: 1.05rem;
}

#cardFiltersTab {
    width: 157px;
}

#packCalcTab {
    width: 135px;
}

#draftCalcTab {
    width: 117px;
}